@import url("https://fonts.googleapis.com/css?family=Jura&&Lato");

* {
  margin: 0;
  padding: 0;
}
.App {
  width: 100vw;
  height: 100vh;
  background: -webkit-linear-gradient(
    335deg,
    rgba(86, 216, 228, 1) 5%,
    rgba(159, 1, 234, 1) 95%
  );
  background: -o-linear-gradient(
    335deg,
    rgba(86, 216, 228, 1) 5%,
    rgba(159, 1, 234, 1) 95%
  );
  background: linear-gradient(
    115deg,
    rgba(86, 216, 228, 1) 5%,
    rgba(159, 1, 234, 1) 95%
  );
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
  position: absolute;
  right: 2rem;
}

.App-header {
  /* width: 100vw; */
  /* border: solid red 1px; */
  padding: 2rem;
}
.logo-title {
  font-size: 2.5em;
  font-family: "Jura", sans-serif;
  -webkit-clip-path: polygon(
    5% 5%,
    35% 0%,
    50% 100%,
    52% 100%,
    37% 0%,
    100% 0%,
    100% 100%,
    0% 100%
  );
          clip-path: polygon(
    5% 5%,
    35% 0%,
    50% 100%,
    52% 100%,
    37% 0%,
    100% 0%,
    100% 100%,
    0% 100%
  );
  background-color: purple;
  display: inline-block;
  width: 15rem;
  padding: 2rem;
  color: white;
  /* text-align: center; */
}

.info {
  position: absolute;
  width: 10rem;
  top: 0;
  height: 30vh;
  background-color: #8b1a89;
  -webkit-animation: polygons 2s infinite;
          animation: polygons 2s infinite;
}
.count {
  color: white;
  text-align: center;
}
.num {
  color: white;
  opacity: 0.5;
  text-align: center;
  font-size: 8rem;
}
.text {
  color: white;
  opacity: 0.6;
  text-align: center;
}
.fetch {
  width: 100%;
  height: 5vh;
  border: none;
  color: white;
  font-size: 2rem;
  background-color: #9b59b6;
  -webkit-box-shadow: 0px 5px 0px 0px #82409d;
          box-shadow: 0px 5px 0px 0px #82409d;
  outline: none;
}

.fetch:active {
  -ms-transform: translate(0px, 5px);
      transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
  -webkit-box-shadow: 0px 1px 0px 0px;
          box-shadow: 0px 1px 0px 0px;
}
@-webkit-keyframes polygons {
  25% {
    -webkit-clip-path: polygon(
      0% 0%,
      35% 0%,
      50% 100%,
      52% 100%,
      37% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
      0% 0%,
      35% 0%,
      50% 100%,
      52% 100%,
      37% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
  }
  50% {
    -webkit-clip-path: polygon(
      0% 0%,
      45% 0%,
      60% 100%,
      62% 100%,
      47% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
      0% 0%,
      45% 0%,
      60% 100%,
      62% 100%,
      47% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
  }
  75% {
    -webkit-clip-path: polygon(
      0% 0%,
      55% 0%,
      70% 100%,
      72% 100%,
      57% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
      0% 0%,
      55% 0%,
      70% 100%,
      72% 100%,
      57% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
  }
}
@keyframes polygons {
