      37% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
  }
  50% {
    -webkit-clip-path: polygon(
      0% 0%,
      45% 0%,
      60% 100%,
      62% 100%,
      47% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
      0% 0%,
      45% 0%,
      60% 100%,
      62% 100%,
      47% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
  }
  75% {
    -webkit-clip-path: polygon(
      0% 0%,
      55% 0%,
      70% 100%,
      72% 100%,
      57% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
      0% 0%,
      55% 0%,
      70% 100%,
      72% 100%,
      57% 0%,
      100% 0%,
      100% 100%,
      0% 100%
    );
  }
}
@media (max-width: 800px) {
  .info {
    position: relative;
    width: 100%;
    top: 0;
    height: 30vh;
    background-color: #8b1a89;
  }
}



